@use "global" as *;

div.colorbox {
  position: relative;
  // width: 80%;
  // margin: 30px auto 30px;
}
div.colorbox div.color {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: #408F95;

}
div.colorbox img {
  display: block;
}