    width: 12.625rem;
    height: 3.5rem;
    z-index: 11;
  }
}

.button p {
  z-index: 10;
  position: absolute;
}

.button::after {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 100%;
  height: 100%;
  border: 0.0625rem solid #108F95;
  z-index: 2;
}

.button span {
  margin-right: 0.125rem;
  margin-left: 0.3125rem;
}

.stickarrow {
  display: inline-block;
  width: 2.5rem;
  height: 0.5rem;
  border-bottom: 0.0625rem solid #fff;
  border-right: 0.0625rem solid #fff;
  transform: skew(45deg);
  padding-bottom: 0.125rem;
  margin: 0rem 0rem 0.125rem 0rem;
}

.button a {
  display: block;
  z-index: 15;
}

.buttonslide {
  position: absolute;
  width: 12.625rem;
  height: 3.375rem;
  top: calc(50% + 0rem);
  left: calc(50% + 0rem);
  transform: translate(-50%, -50%);
  z-index: 3;
}

.buttonslide::after {
  background: gray;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.buttonslide:hover {
  color: #fff;
  z-index: 5;
}

.buttonslide:hover::after {
  transform: scale(1, 1);
}

.buttonslide:hover + p {
  z-index: 6; /* .button p の z-index を増やす */
}

.buttonslide:hover .button::after {
  transform: scale(1, 1);
}

.buttonslide:hover {
  color: #fff;
  z-index: 5;
}

