}

.swiper-button-next {
  position: absolute;
  display: none;
}
@media screen and (min-width: 768px) {
  .swiper-button-next {
    display: block;
    top: calc(50% - 17.75rem);
    left: calc(50% + 30.875rem);
    transform: translate(-50%, -50%);
    height: 5rem;
    width: 5rem;
  }
}

